<template>
<div :style='{"width":"100%","padding":"100px 18% 60px","margin":"0px auto","position":"relative","background":"url(http://codegen.caihongy.cn/20231029/b89b677f7bba41d694305c19dc3b820d.jpg) no-repeat center top,#fff"}'>
    <el-form
	  :style='{"border":"1px solid #333","width":"100%","padding":"30px","position":"relative","background":"#fcfcfc"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏名称" prop="youximingcheng">
            <el-input v-model="ruleForm.youximingcheng" 
                placeholder="游戏名称" clearable :disabled=" false  ||ro.youximingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏分类" prop="youxifenlei">
            <el-input v-model="ruleForm.youxifenlei" 
                placeholder="游戏分类" clearable :disabled=" false  ||ro.youxifenlei"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏封面" v-if="type!='cross' || (type=='cross' && !ro.youxifengmian)" prop="youxifengmian">
            <file-upload
            tip="点击上传游戏封面"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.youxifengmian?ruleForm.youxifengmian:''"
            @change="youxifengmianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' class="upload" v-else label="游戏封面" prop="youxifengmian">
                <img v-if="ruleForm.youxifengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.youxifengmian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.youxifengmian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏厂商" prop="youxichangshang">
            <el-input v-model="ruleForm.youxichangshang" 
                placeholder="游戏厂商" clearable :disabled=" false  ||ro.youxichangshang"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏时长" prop="youxishizhang">
            <el-input v-model.number="ruleForm.youxishizhang" 
                placeholder="游戏时长" clearable :disabled=" false  ||ro.youxishizhang"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏优点" prop="youxiyoudian">
            <el-input v-model="ruleForm.youxiyoudian" 
                placeholder="游戏优点" clearable :disabled=" false  ||ro.youxiyoudian"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="游戏缺点" prop="youxiquedian">
            <el-input v-model="ruleForm.youxiquedian" 
                placeholder="游戏缺点" clearable :disabled=" false  ||ro.youxiquedian"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="个人体验" prop="gerentiyan">
            <el-input v-model="ruleForm.gerentiyan" 
                placeholder="个人体验" clearable :disabled=" false  ||ro.gerentiyan"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'  label="游戏评分" prop="youxipingfen">
            <el-select v-model="ruleForm.youxipingfen" placeholder="请选择游戏评分" :disabled=" false  ||ro.youxipingfen" >
              <el-option
                  v-for="(item,index) in youxipingfenOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="用户名" prop="yonghuming">
            <el-input v-model="ruleForm.yonghuming" 
                placeholder="用户名" clearable :disabled=" false  ||ro.yonghuming"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="反馈日期" prop="fankuiriqi">
              <el-date-picker
				  :disabled="true  ||ro.fankuiriqi"
                  format="yyyy 年 MM 月 dd 日"
                  value-format="yyyy-MM-dd"
                  v-model="ruleForm.fankuiriqi" 
                  type="date"
                  placeholder="反馈日期">
              </el-date-picker> 
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="测试反馈" prop="ceshifankui">
            <editor 
                :style='{"minHeight":"250px","padding":"0","margin":"0","borderColor":"#1abc9e30","backgroundColor":"none","borderRadius":"0","borderWidth":"0px","background":"#fff","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.ceshifankui" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"4px","background":"#833cf6","width":"110px","lineHeight":"36px","fontSize":"14px","height":"36px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"0px solid #eee","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"4px","background":"#000","width":"110px","lineHeight":"36px","fontSize":"14px","height":"36px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				youximingcheng : false,
				youxifenlei : false,
				youxifengmian : false,
				youxichangshang : false,
				youxishizhang : false,
				youxiyoudian : false,
				youxiquedian : false,
				gerentiyan : false,
				youxipingfen : false,
				ceshifankui : false,
				yonghuming : false,
				fankuiriqi : false,
				crossuserid : false,
				crossrefid : false,
				shhf : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          youximingcheng: '',
          youxifenlei: '',
          youxifengmian: '',
          youxichangshang: '',
          youxishizhang: '',
          youxiyoudian: '',
          youxiquedian: '',
          gerentiyan: '',
          youxipingfen: '',
          ceshifankui: '',
          yonghuming: '',
          fankuiriqi: '',
          crossuserid: '',
          crossrefid: '',
        },
        youxipingfenOptions: [],


        rules: {
          youximingcheng: [
          ],
          youxifenlei: [
          ],
          youxifengmian: [
          ],
          youxichangshang: [
          ],
          youxishizhang: [
            { required: true, message: '游戏时长不能为空', trigger: 'blur' },
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          youxiyoudian: [
          ],
          youxiquedian: [
          ],
          gerentiyan: [
          ],
          youxipingfen: [
            { required: true, message: '游戏评分不能为空', trigger: 'blur' },
          ],
          ceshifankui: [
          ],
          yonghuming: [
          ],
          fankuiriqi: [
          ],
          crossuserid: [
          ],
          crossrefid: [
          ],
          shhf: [
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.fankuiriqi = this.getCurDate()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='youximingcheng'){
              this.ruleForm.youximingcheng = obj[o];
              this.ro.youximingcheng = true;
              continue;
            }
            if(o=='youxifenlei'){
              this.ruleForm.youxifenlei = obj[o];
              this.ro.youxifenlei = true;
              continue;
            }
            if(o=='youxifengmian'){
              this.ruleForm.youxifengmian = obj[o].split(",")[0];
              this.ro.youxifengmian = true;
              continue;
            }
            if(o=='youxichangshang'){
              this.ruleForm.youxichangshang = obj[o];
              this.ro.youxichangshang = true;
              continue;
            }
            if(o=='youxishizhang'){
              this.ruleForm.youxishizhang = obj[o];
              this.ro.youxishizhang = true;
              continue;
            }
            if(o=='youxiyoudian'){
              this.ruleForm.youxiyoudian = obj[o];
              this.ro.youxiyoudian = true;
              continue;
            }
            if(o=='youxiquedian'){
              this.ruleForm.youxiquedian = obj[o];
              this.ro.youxiquedian = true;
              continue;
            }
            if(o=='gerentiyan'){
              this.ruleForm.gerentiyan = obj[o];
              this.ro.gerentiyan = true;
              continue;
            }
            if(o=='youxipingfen'){
              this.ruleForm.youxipingfen = obj[o];
              this.ro.youxipingfen = true;
              continue;
            }
            if(o=='ceshifankui'){
              this.ruleForm.ceshifankui = obj[o];
              this.ro.ceshifankui = true;
              continue;
            }
            if(o=='yonghuming'){
              this.ruleForm.yonghuming = obj[o];
              this.ro.yonghuming = true;
              continue;
            }
            if(o=='fankuiriqi'){
              this.ruleForm.fankuiriqi = obj[o];
              this.ro.fankuiriqi = true;
              continue;
            }
            if(o=='crossuserid'){
              this.ruleForm.crossuserid = obj[o];
              this.ro.crossuserid = true;
              continue;
            }
            if(o=='crossrefid'){
              this.ruleForm.crossrefid = obj[o];
              this.ro.crossrefid = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.youxipingfenOptions = "一星,二星,三星,四星,五星".split(',')

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`ceshifankui/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('ceshifankui/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`ceshifankui/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`ceshifankui/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      youxifengmianUploadChange(fileUrls) {
          this.ruleForm.youxifengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
